.field-customizer{background-color:#eee;border-bottom:5px solid #eee;border-radius:3px;width:250px}.field-customizer fieldset{border:0;padding-bottom:10px;padding-top:10px}.field-customizer form{height:1.8em}.field-customizer label{display:inline-block;font-size:11px !important;padding-right:10px;text-align:left;width:75px}.field-customizer ul{border-bottom:1px solid #aaa;margin:0 0;padding:2px;height:18px}.field-customizer li{display:inline-block;border:1px solid rgba(0,0,0,0);border-bottom:none;bottom:-1px;position:relative;list-style:none;padding:2px 10px;cursor:pointer;font-size:12px}