.log-pane,.warning-pane,.error-pane{margin-left:-100px;z-index:10;max-width:500px;position:absolute;top:15px;padding:15px;border:1px solid rgba(0,0,0,0);border-radius:4px;left:50%}.log-pane ul,.warning-pane ul,.error-pane ul{margin:0;padding:0}.log-pane li,.warning-pane li,.error-pane li{list-style-type:none;margin:5px;padding-left:10px}.error-pane{background-color:#f2dede;color:#a94442}.warning-pane{background-color:#fff9e5;color:#fc0}.close{position:absolute;top:5px;right:5px}