.plot-group,.plot-list-item-group{background-color:#fff;overflow:hidden;text-align:center;padding:8px;max-height:600px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column}.plot-list-item-group{margin:4px 4px 0 0;-webkit-box-flex:1;-ms-flex-positive:1;flex-grow:1;max-height:290px}.plot{overflow:hidden;padding-right:11px}.plot-scroll{overflow:auto;padding-right:0px}.plot-info{text-align:left;margin-bottom:6px}.plot-field-info{display:inline-block;margin-right:4px}.command-toolbox{float:right}.command,.specify-command,.sort-x-command{cursor:pointer;margin-left:10px;color:#aaa}.sort-x-command{-webkit-transform:rotateZ(270deg);transform:rotateZ(270deg)}.command:hover,.specify-command:hover,.sort-x-command:hover{color:#72c1d5}.specify-command{-webkit-transform:scaleX(-1);transform:scaleX(-1)}.copied{font-size:12px}.note{min-height:60px;resize:none;border-color:#d3d3d3;width:100%}