.placeholder,.placeholder-disabled,.placeholder-over,.placeholder-active{display:block;width:100%;padding:0 .5em;border:1px solid hsla(0,0%,30%,.1);background-color:#fff;line-height:20px;border-radius:3px;text-overflow:ellipsis;font-weight:normal;color:#ccc}.placeholder-over,.placeholder-active{border:1px dashed #4caf50;background-color:#d0efb3;color:#212121}.placeholder-active{background-color:#c8e6c9}.placeholder-disabled{background-color:#e0e0e0;color:gray}.encoding-shelf,.wildcard-shelf{background-color:hsla(224,25%,29%,.2);border:1px solid #ddd;border-radius:3px;display:-webkit-box;display:-ms-flexbox;display:flex;font-size:11px;margin-bottom:4px;width:100%;height:22px}.field-wrapper{-webkit-box-flex:1;-ms-flex-positive:1;flex-grow:1;box-sizing:border-box;border:1px solid rgba(0,0,0,0);border-radius:3px;width:168px;height:22px}.wildcard-shelf{background-color:hsla(185,80%,29%,.4)}.shelf-label{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;display:block;width:52px;margin:0 .2rem;line-height:22px;-ms-flex-negative:0;flex-shrink:0}.shelf-label :global .fa{margin-top:4px;opacity:.5}.overlay-screen{z-index:1;position:fixed;top:0;bottom:0;left:0;right:0}